.floating-icons {
  position: fixed;
  bottom: 20px;
  right: 20px;
  display: flex;
  flex-direction: column;
  gap: 12px;
  z-index: 1000;
}

.icon-btn {
  background-color: rgba(0, 0, 0, 0.7);
  color: white;
  font-size: 20px;
  padding: 12px 14px;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  text-align: center;
  text-decoration: none;
  transition: background-color 0.3s ease;
}

.icon-btn:hover {
  background-color: #91c8b0;
}

@media (max-width: 768px) {
  .floating-icons {
    display: none;
  }
}